.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /* Landning sida */

  background: #FFFFFF;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.menu {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 46px 36px;
  gap: 60px;

  position: absolute;
  width: 925px;
  height: 154px;
  left: 587px;
  top: 0px;
}
p{
  width: 125px;
  height: 36px;

  font-family: 'Inter';
  font-style: normal;
  font-weight: 800;
  font-size: 10px;
  line-height: 36px;

  color: #000000;


  /* Inside auto layout */
  flex: none;
  order: 3;
  flex-grow: 0;
  
}

/*The navbar container */
nav {
  background-color: lightgray;
  height: 85px;
  display: flex;
  justify-content: space-between;
  padding: 0.2rem calc((100vw - 1000px) / 2);
  z-index: 12;
}

nav .logo {
  width: auto;
  height: 100%;
  padding: 1em;
}

nav .logo img {
  height: 100%;
  width: auto;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

nav li {
  flex: 1;
  text-align: center;
}

nav a {
  display: block;
  padding: 1em;
  background-color: #ffffff;
  text-decoration: none;
  color: black;
  font-size: 1.2rem;
  font-weight: bold;
}

nav a:hover {
  background-color: #4d4dff;
  color: white;
}

/* FOOTER */
.footer-basic {
  padding:40px 0;
  background-color:#ffffff;
  color:#4b4c4d;
}

.footer-basic ul {
  padding:0;
  list-style:none;
  text-align:center;
  font-size:18px;
  line-height:1.6;
  margin-bottom:0;
}

.footer-basic li {
  padding:0 10px;
}

.footer-basic ul a {
  color:inherit;
  text-decoration:none;
  opacity:0.8;
}

.footer-basic ul a:hover {
  opacity:1;
}

.footer-basic .social {
  text-align:center;
  padding-bottom:25px;
}

.footer-basic .social > a {
  font-size:24px;
  width:40px;
  height:40px;
  line-height:40px;
  display:inline-block;
  text-align:center;
  border-radius:50%;
  border:1px solid #ccc;
  margin:0 8px;
  color:inherit;
  opacity:0.75;
}

.footer-basic .social > a:hover {
  opacity:0.9;
}

.footer-basic .copyright {
  margin-top:15px;
  text-align:center;
  font-size:13px;
  color:#aaa;
  margin-bottom:0;
}

.upload-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.input-field {
  margin: 10px 0;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid gray;
  font-size: 16px;
}

.submit-button {
  margin: 20px 0;
  padding: 10px 20px;
  background-color: blue;
  color: white;
  border-radius: 5px;
  border: none;
}
.errormessage{
  color: red
}

/* Landingssida */
.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
  padding: 2rem;
  background: linear-gradient(to bottom, #ff8000, #573700);
}

.header {
  font-size: 2rem;
  color: white;
  animation: fadeIn 1s ease-in-out;
}

.subheader {
  font-size: 1.5rem;
  margin-top: 1rem;
  color: white;
  animation: fadeIn 1s ease-in-out 1s;
}

.content {
  font-size: 1.25rem;
  margin-top: 1rem;
  color: white;
  animation: fadeIn 1s ease-in-out 2s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
